<template>
    <div class="wrapper">
        <div id="app">
            <form @submit.prevent="submit">
                <header>{{headerText|toLowwer}};{{headerText|toUpper}}</header>
                <div class="field">
                    <input type="text" v-model="user1.name">
                </div>

                <div class="field">
                    
                    <label>
                        <input type="radio" value="" v-model="user1.gender"> 
                    </label>
                    <label>
                        <input type="radio" value="" v-model="user1.gender"> 
                    </label>
                </div>
                <input type="submit" value="">
            </form>
            <form>
                <input type="text" value="" v-model="user2.name" placeholder="">
                <input type="text" value="" v-model="user2.age" placeholder="">
                <input type="file" @change="getFile($event)">
                <button @click="submitForm($event)"></button>
            </form>
        </div>
    </div>
</template>

<style>
    .wrapper {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
    }

</style>

<script>
    import Axios from '../axios/index'
    import Vue from 'vue'
    import {routerMixin} from '../mixins/mixins'
    import {toLowwer, toUpper} from '../filters/filter'
    export default {
        data(){
            return{
                headerText: 'hello world helloword',//
                user1: {
                    name: '',
                    gender: ''
                },
                user2:{
                    name: '',
                    age: '',
                    file: ''
                }
            }
        },
        mixins: [routerMixin],
        filters: {
            toLowwer: toLowwer,
            toUpper: toUpper
        },
        created: function () {
            console.log('')// methods, components  directives
        },
        methods: {
            hello: function(){
                console.log('hello ')//
            },
            submit: function() {
                let me = this
                let formData = JSON.stringify(me.user1); // www-form
                me.$axios({
                    url: Axios.submitForm,
                    method: 'post',
                    data: formData
                })
                    .then(function (res) {
                        console.log(res)
                    },function (res) {
                        console.log(res)
                    })
                    .catch(function (res) {
                        console.log(res)
                    })
            },
            getFile(event) {
                let me = this
                me.user2.file = event.target.files[0];
                console.log(me.file);
            },
            submitForm(event) {
                let me = this
                event.preventDefault();
                let formData = new FormData();
                formData.append('name', me.user2.name);
                formData.append('age', me.user2.age);
                formData.append('file', me.user2.file);


                me.$axios({
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    },
                    url: Axios.submitForm,
                    method: 'post',
                    data: formData
                })
                    .then(function (res) {
                        console.log(res)
                    },function (res) {
                        console.log(res)
                    })
                    .catch(function (res) {
                        console.log(res)
                    })
            }
        }
    }
</script>


