import Vue from 'vue'
import Router from 'vue-router'
import Canvas from '../pages/canvas.vue'
import FileForm from '../pages/fileform.vue'
Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/', //
            component: r => require.ensure(//import
                [],
                () => r(require('../pages/articleList.vue')),
                'articleList'
            )
        },
        {
            path: '/articleList', //
            component: r => require.ensure(
                [],
                () => r(require('../pages/articleList.vue')),
                'articleList'
            )
                /*resolve => require(['../pages/articleList.vue'], resolve)*/
        },
        {
            path: '/manAccount', //
            component: r => require.ensure(
                [],
                () => r(require('../pages/manAccount.vue')),
                'manAccount'
            )
        },
        {
            path: '/canvas',//canvas
            component: Canvas

        },
        {
            path: '/websocket',//
            component: r => require.ensure(//import
                [],
                () => r(require('../pages/webSocket.vue')),
                'websocket'
            )

        },
        {
            path: '/fileForm',//
            component: FileForm

        },
        {
            path: '*', //
            redirect: '/'
        }
    ]
})



