import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

export default new Vuex.Store({
    strict: process.env.NODE_ENV !== 'production', //
    state: {
        // 
        user: '',
        isDevFlag: process.env.NODE_ENV === 'development'
    },
    mutations: {
        login (state, param) {
            state.user = param
        }
    }
})



