export var globalMixin = {
    created: function () {
        console.log('')
        this.hello()
    },
    methods: {
        hello: function () {
            console.log('hello from mixin!')
        }

    }
};
export var routerMixin = {
    methods: {
        jump (to) {
            if (this.$router) {
                this.$router.push(to)
            }
        }
    }
};



